.navbar {
  background-color: transparent;
  position: fixed;
  top: 0;
  height: 90px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "Affogato", "sans-serif";
  font-weight: 500;
  align-items: center;
  padding: 20px 0;
  transition: all 0.15s ease-in-out;
}
.navbar .logo-div {
  padding: 15px 20px;
  cursor: pointer;
}
.navbar .nav-logo {
  min-width: 233px;
  width: 233px;
  transition: min-width 0.15s ease-in-out;
}
.navbar .nav-logo-sticky {
  display: none;
}
.navbar .nav-logo-mobile {
  width: 45px;
  display: none;
}
.navbar .nav-right {
  display: flex;
  align-items: center;
}
.navbar .nav-right .navs {
  display: flex;
  color: white;
  align-items: center;
}
.navbar .nav-right .navs .nav {
  margin-right: 27px;
  white-space: nowrap;
  font-size: 16px;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  -webkit-user-select: none; /* Safari */ /* IE 10 and IE 11 */
  -moz-user-select: none;
       user-select: none; /* Standard syntax */
}
.navbar .nav-right .navs .nav:hover {
  opacity: 0.7;
  transition: opacity 0s ease-in-out;
}
.navbar .nav-right .language-btn {
  display: flex;
  align-items: center;
  background: #f7f1ff;
  border-radius: 100px;
  padding: 11px 16px 11px 18px;
  margin: 15px 30px 15px 0;
  outline: none;
  border: none;
  min-width: -moz-fit-content;
  min-width: fit-content;
  cursor: pointer;
  -webkit-user-select: none; /* Safari */ /* IE 10 and IE 11 */
  -moz-user-select: none;
       user-select: none; /* Standard syntax */
}
.navbar .nav-right .language-btn .flag {
  margin-right: 12px;
  width: 22.4px;
  height: 16px;
}
.navbar .nav-right .hamburger {
  padding: 22px;
  display: none;
}
.navbar .outline-btn {
  background-color: transparent;
  outline: none;
  border: 1px solid #ffffff;
  border-radius: 100px;
  color: white;
  padding: 8px 25px;
  white-space: nowrap;
  font-weight: 600;
  margin-right: 16px;
}
.navbar .outline-btn:hover {
  cursor: pointer;
  background-color: white;
  color: #7e32d4;
  transition: all 0.15s ease-in-out;
}
.navbar.sticky {
  background-color: white;
  position: fixed;
  top: 0;
  transition: all 0.15s ease-in-out;
}
.navbar.sticky .nav-logo-sticky {
  display: block;
  min-width: 233px;
  width: 233px;
  transition: min-width 0.15s ease-in-out;
}
.navbar.sticky .nav-logo {
  display: none;
}
.navbar.sticky .nav {
  color: #575559;
  transition: all 0.15s ease-in-out;
}
.navbar.sticky .outline-btn.green {
  border-color: #00b1ac;
  color: #00b1ac;
  transition: all 0.15s ease-in-out;
}
.navbar.sticky .outline-btn.green:hover {
  background-color: #00b1ac;
  color: white;
}
.navbar.sticky .outline-btn.purple {
  border-color: #7e32d4;
  color: #7e32d4;
  transition: all 0.15s ease-in-out;
}
.navbar.sticky .outline-btn.purple:hover {
  background-color: #7e32d4;
  color: white;
}
.navbar.mobile .nav-logo-mobile {
  display: block;
}
.navbar.mobile .nav-logo {
  display: none;
}
.navbar.mobile .navs {
  display: none;
}
.navbar.mobile .language-btn {
  margin-right: 0;
}
.navbar.mobile .hamburger {
  display: block;
  cursor: pointer;
}
.navbar.mobile .hamburger svg {
  transform: rotate(0deg);
  transition: transform 0.3s ease;
}
.navbar.mobile .hamburger:hover svg {
  transform: rotate(180deg);
  transition: transform 0.3s ease;
}
.navbar.mobile.sticky .nav-logo-sticky {
  display: none;
}
.navbar.mobile.sticky .hamburger svg line {
  stroke: #7e32d4;
}

.mobile-menu {
  position: fixed;
  top: 90px;
  right: 0;
  background-color: white;
  width: auto;
  text-align: center;
  padding: 20px 20px 10px;
  display: flex;
  flex-direction: column;
  border-radius: 10px 0 0 10px;
  transition: 0.15s ease-out;
  border: solid 1px #7e32d4;
  border-right: none;
}
.mobile-menu .nav {
  margin-bottom: 14px;
  cursor: pointer;
  transition: opacity 0s ease-in-out;
  -webkit-user-select: none; /* Safari */ /* IE 10 and IE 11 */
  -moz-user-select: none;
       user-select: none; /* Standard syntax */
}
.mobile-menu .nav:hover {
  opacity: 0.7;
  transition: opacity 0.1s ease-in-out;
}
.mobile-menu .outline-btn {
  margin-bottom: 10px;
  margin-right: 0;
  cursor: pointer;
  -webkit-user-select: none; /* Safari */ /* IE 10 and IE 11 */
  -moz-user-select: none;
       user-select: none; /* Standard syntax */
}
.mobile-menu .outline-btn.green {
  border-color: #00b1ac;
  color: #00b1ac;
  transition: all 0.2s ease-in-out;
}
.mobile-menu .outline-btn.green:hover {
  background-color: #00b1ac;
  color: white;
}
.mobile-menu .outline-btn.purple {
  border-color: #7e32d4;
  color: #7e32d4;
  transition: all 0.2s ease-in-out;
}
.mobile-menu .outline-btn.purple:hover {
  background-color: #7e32d4;
  color: white;
}

.mobile-menu.hide {
  right: -260px;
  opacity: 0;
  transform: translateX(0%);
  transition: 0.15s ease-in;
}

